<script>
const Cosmic = require('cosmicjs')()
const bucket = Cosmic.bucket({
  slug: 'mattialerda-bucket',
  read_key: 'ooDtNqsMWF0yNqxZI5Ytz4e5NO3q3OWOYg2kCYtJqy37lV4BVo'
})
